import React from 'react'
import styled from 'styled-components'
import { AutoColumn } from '../Column'
import i18next from 'i18next'
import LanguageOption from './LanguageOption'

const StyledMenu = styled.div`
  padding: 0 36px;
  display: flex;
  position: relative;
  text-align: start;

  > * {
    width: 100%;
  }
`
export default function LanguageTab() {
  const currentLanguage = i18next.language || 'en'
  const lang = currentLanguage.substring(0, 2)
  document.body.dir = i18next.dir(lang!)

  return (
    <StyledMenu>
      <AutoColumn gap="8px">
        <LanguageOption shortCode="ar" languageString="" />
        <LanguageOption shortCode="cn" languageString="" />
        <LanguageOption shortCode="de" languageString="Deutsch" />
        <LanguageOption shortCode="en" languageString="English" />
        <LanguageOption shortCode="es" languageString="Espaol" />
        <LanguageOption shortCode="fa" languageString="" />
        <LanguageOption shortCode="fr" languageString="Franais" />
        <LanguageOption shortCode="it" languageString="Italiano" />
        <LanguageOption shortCode="jp" languageString="" />
        <LanguageOption shortCode="kr" languageString="" />
        <LanguageOption shortCode="nl" languageString="Nederlands" />
        <LanguageOption shortCode="ro" languageString="Romn" />
        <LanguageOption shortCode="pt" languageString="Portugus" />
        <LanguageOption shortCode="ru" languageString="P" />
        <LanguageOption shortCode="se" languageString="Svenska" />
        <LanguageOption shortCode="tr" languageString="Trke" />
        <LanguageOption shortCode="vn" languageString="Ting Vit" />
      </AutoColumn>
    </StyledMenu>
  )
}
